.section {

}

.section--padding {
  padding-top: 70px;
  padding-bottom: 50px;
}

.section--first-screen {
  align-self: flex-start;
  @include media-breakpoint-up('md') {
    p {
      max-width: (506em/16);
      width: 60%;
    }
    .socials-links {
      position: absolute;
      top: (-70px + 37px);
      bottom: (-50px + 37px);
      right: (21rem/16);
    }
    .container {
      position: relative;
      min-height: 200px;
      padding-right: (66rem/16);
    }
  }
}

.section--first-screen.ibg {
  margin-bottom: auto;
}
