.grid-works {
  &__nav {

  }

  &__wrapper { // min-width img - 544px
    display: flex;
    flex-wrap: wrap;
    max-width: 514px;
    margin-left: auto;
    margin-right: auto;
    @media (min-width: 790px) {
      max-width: none;
    }
    @media (min-width: 992px) {
      max-width: 920px;
    }
    @media (min-width: 1200px) {
      max-width: none;
    }
  }

  &__item {
    display: flex;
    width: 100%;
    align-items: flex-end;
    color: $color-text;
    position: relative;
    overflow: hidden;
    @media (min-width: 790px) {
      width: 50%;
    }
    @media (min-width: 1200px) {
      width: 33.333%;
    }
    @media (min-width: 1600px) {
      width: 25%;
    } 
    @media (min-width: 1920px) {
      width: 400px;
      flex: 1 1 auto;
      max-width: 25%;
    }
    &:hover {
      color: $color-text;
      &::after {
        opacity: 1;
      }
    }
    &::before {
      content: '';
      padding-top: 56.25%;
      width: 0;
      display: block;
    }
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.35);
      opacity: 0;
      transition: opacity $transition;
    }
  }

  &__item:hover &__inner {
    transform: translate(0, 0);
    opacity: 1;
  }

  &__inner {
    padding: (20rem/16) (25rem/16);
    position: relative;
    z-index: 1;
    transform: translate(0, 50%);
    opacity: 0;
    transition: transform $transition, opacity $transition;
    .h3 {
      margin: 0;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
  }

  &__tag {
    white-space: nowrap;
    text-transform: lowercase;
    &::after {
      content: '/';
      margin-left: 0.3em;
      margin-right: 0.3em;
    }
  }
  &__tag:last-child {
    &::after {
      content: '';
      display: none;
    }
  }
}

.touch-browser .grid-works__item::after {
  opacity: 1;
}
.touch-browser .grid-works__inner {
  transform: translate(0, 0);
  opacity: 1;
}